import React, {useState} from 'react';
import './App.css';
import {TopBar, Intro, MusicBlock} from './Components'
import {audioData} from './Data'
import {Grid, CssBaseline, MuiThemeProvider} from "@material-ui/core";

import {createMuiTheme} from '@material-ui/core';

const App = () => {
    const [theme, setTheme] = useState({
        palette:{
            type:"dark"
        }
    });

    const toggleDarkTheme = () => {
        let newPaletteType = theme.palette.type === "light" ? "dark" : "light";
        setTheme({
            palette:{
                type:newPaletteType
            }
        });
    };

    const muiTheme = createMuiTheme(theme);

    const AudioOutput = () => {
        return (
            <Grid container id={"music"}>
                {audioData.map(info =>
                    <Grid item xs={12} sm={6} lg={3}>
                        <MusicBlock
                            name={info.name}
                            type={info.type}
                            auth={info.auth}
                            date={info.date}
                            img={info.img}
                            audio={info.audio}
                            href={info.href}
                        />
                    </Grid>
                )}
            </Grid>
        )
    };

    return (
        <MuiThemeProvider theme={muiTheme}>
            <CssBaseline/>
            <TopBar onToggleDark={toggleDarkTheme}/>
            <Intro/>
            <AudioOutput/>
        </MuiThemeProvider>
    );
};

export default App;
